<template>
  <div class="root">
    <div ref="sub" class="sub_title" :style="{ marginTop: margin_top }">
      Check your camera
    </div>
    <div class="option" :style="{ height: option_height }">
      <v-btn text class="btn ok_btn" @click="gotoLive">
        CONTINUE
      </v-btn>
      <v-btn text class="btn cancle_btn" @click="gotoHome">
        BACK
      </v-btn>
    </div>
  </div>
</template>

<script>
import { publishVideo } from "../utils/webrtc/publish";

export default {
  data() {
    return {
      margin_top: "0px",
      option_height: "0px"
    };
  },
  created() {
    this.$nextTick(() => {
      const sub_height = this.$refs.sub.getBoundingClientRect().height;
      const v_height = window.innerHeight * 0.15;
      this.margin_top = v_height - sub_height - 10 + "px";

      const height =
        document.documentElement.clientHeight - window.innerHeight * 0.75;
      this.option_height = height - 20 + "px";
    });
  },
  methods: {
    gotoLive() {
      console.log("PUBLISH");
      publishVideo();

      this.$router.push("/event/live");
    },
    gotoHome() {
      this.$router.push("/");
    }
  }
};
</script>

<style scoped>
.root {
  position: absolute;
  width: 100%;
  height: 100%;
}

.sub_title {
  text-align: center;
  font-weight: bold;
  font-size: 25px;
  color: #666666;
  position: relative;
  bottom: 0;
}

.option {
  position: fixed;
  width: 100%;
  bottom: 0;

  display: flex;
  flex-direction: column;
}

.btn {
  margin-bottom: 5px;
  font-size: 20px;
  /* font-weight: bold; */
}

.ok_btn {
  color: #03dac5;
}

.cancle_btn {
  color: #666666;
}
</style>