<template>
  <video id="player" ref="hls_player" muted controls></video>
</template>

<script>
import { mapGetters } from "vuex";
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

const Hls = window.Hls;
const hls = new Hls();

export default {
  data() {
    return {
      window_width: 0,
      window_height: 0
    };
  },
  computed: {
    ...mapGetters(["getHlsUrl"])
  },
  props: ["props_tab"],
  created() {
    this.$nextTick(() => {
      const v_height = (window.innerWidth * 9) / 16;
      this.$refs.hls_player.style.height = v_height + "px";
    });
    this.$store.dispatch("LOAD_HLS_URL").then(() => {
      if (Hls.isSupported()) {
        hls.attachMedia(this.$refs.hls_player);
        hls.on(Hls.Events.MEDIA_ATTACHED, () => {
          hls.loadSource(this.getHlsUrl);
        });
      }
    });

    EventBus.$on(EVENT_SET_EVENT_TYPE, path => {
      if (path === "/event/live") {
        this.playVideo();
      }
    });
  },
  methods: {
    playVideo() {
      this.$refs.hls_player.play();
    }
  }
};
</script>

<style>
#player {
  background-color: black;
  width: 100vw;
}
</style>
