<template>
  <div class="root">
    <img ref="logo" class="logo_img" src="../assets/Logo-white.png" />
    <ul ref="rlist" class="chatting_list">
      <li v-for="msg in getMessages" :key="msg.index" class="msg_obj">
        <div class="top"></div>
        <div>
          <div v-if="msg.isMine" class="msg_horizontal msg_right">
            <div class="msg_time">
              {{ getFormatDate(msg.time) }}
            </div>
            <div class="msg_item msg_mine">
              <div>
                {{ msg.message }}
              </div>
            </div>
          </div>
          <div v-else>
            <div class="msg_top">
              {{ msg.user }}
            </div>
            <div class="msg_horizontal">
              <div class="msg_item msg_other">
                <div>
                  {{ msg.message }}
                </div>
              </div>
              <div class="msg_time">
                {{ getFormatDate(msg.time) }}
              </div>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { EventBus, EVENT_UPDATE_LIST } from "../utils/event-bus";

export default {
  data() {
    return {
      msg_right: "msg_right",
      msg_mine: "msg_mine",
      msg_other: "msg_other",
      color_mine: "#03dac5",
      color_other: "#f0f1f5"
    };
  },
  computed: {
    ...mapGetters(["getMessages"])
  },
  created() {
    EventBus.$on(EVENT_UPDATE_LIST, () => {
      this.scrollToBottom();
    });

    this.$nextTick(() => {
      const rlist = this.$refs.rlist;
      const h = window.innerHeight - rlist.getBoundingClientRect().y - 50;
      rlist.style.height = h + "px";
      // rlist.style.width = window.innerWidth+ "px"

      const v_h = (window.innerWidth * 9) / 16;
      this.$refs.logo.style.marginTop = v_h + "px";
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.scrollToBottom();
    });
  },
  watch: {
    getMessages() {
      console.log("add item");
      this.$nextTick(function() {
        this.scrollToBottom();
      });
    }
  },
  methods: {
    getFormatDate(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString("en-US", {
        hour: "numeric",
        minute: "numeric",
        hour12: true
      });
    },
    scrollToBottom() {
      const rlist = this.$refs.rlist;
      rlist.scrollTop = rlist.scrollHeight;
    }
  }
};
</script>

<style scoped>
.root {
  /* background-color: #f5f6f1; */
}

.chatting_list {
  width: 100%;
  overflow-y: scroll;
  list-style: none;
}

.msg_item {
  box-sizing: border-box;
  width: fit-content;
  max-width: 70%;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  font-size: 15px;
  margin: 0;
  word-wrap: break-word;
  padding: 10px;
  margin-bottom: 15px;
}

.msg_obj {
  margin-left: -15px;
}

.msg_top {
  margin-left: 5px;
  font-size: 13px;
  color: #666666;
}

.msg_time {
  padding: 0;
  margin: 0px 8px;
  color: #707070;
  font-weight: normal;
  font-size: 12px;
  display: flex;
  align-items: flex-end;
  margin-bottom: 15px;
}

.chat_text {
  font-size: 20px;
}

.msg_right {
  display: flex;
  justify-content: flex-end;
  text-align: right;
  margin-right: 10px;
}

.msg_mine {
  background-color: #03dac5;
  color: #ffffffde;
}

.msg_other {
  background-color: #f0f1f5;
  color: #666666;
}

.logo_img {
  position: fixed;
  margin: 0;
  top: 10px;
  right: 10px;
  height: 10vh;
  pointer-events: none;
}

.msg_horizontal {
  display: flex;
}

.top {
  width: 100%;
  height: 5px;
}
</style>
