<template>
  <div>
    <div class="root">
      <HLSVideo :style="{ display: is_video_show }"></HLSVideo>
      <router-view class="contents"></router-view>
    </div>
    <div :class="{ preset_sender: !is_live_time, live_sender: is_live_time }">
      <WebRTCSender
        :style="{ marginTop: margin_top, boxShadow: shadow }"
        :width="sender_width"
        :height="sender_height"
        class="sender"
      />
    </div>
  </div>
</template>

<script>
import HLSVideo from "@/components/HLSVideo.vue";
import WebRTCSender from "@/components/WebRTCSender.vue";
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

export default {
  data() {
    return {
      margin_top: "0px",
      is_live_time: false,
      is_video_show: "none",
      sender_width: 0,
      sender_height: 0,
      shadow: "3px 6px 5px 0px #00000016"
    };
  },
  components: {
    WebRTCSender,
    HLSVideo
  },
  created() {
    EventBus.$on(EVENT_SET_EVENT_TYPE, () => {
      this.setLiveStatus();
    });

    const v_height = window.innerHeight * 0.6;
    this.sender_width = (v_height * 9) / 16;
    this.sender_height = v_height;
    this.margin_top = window.innerHeight * 0.15 + "px";

    if (this.$router.history.current.path === "/event/live") {
      this.setLiveStatus();
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    gotoHome() {
      this.$router.push("/info");
    },
    setLiveStatus() {
      console.log("!!!!");
      this.shadow = "none";
      this.is_live_time = true;
      this.is_video_show = "block";
      this.margin_top = "0px";
      const video_height = ((window.innerWidth * 9) / 16) * 0.5;
      this.sender_width = (video_height * 9) / 16;
      this.sender_height = video_height;
    }
  }
};
</script>

<style scoped>
.contents {
  width: 100%;
}

.preset_sender {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  display: flex;
  /* align-items: center; */
  justify-content: center;
}

.sender {
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background-color: black;
}

.shadow {
  box-shadow: 3px 6px 5px 0px #00000016;
}

.live_sender {
  position: absolute;
  display: flex;
  right: 5px;
  top: 15px;
  /* align-content: flex-start;
  justify-items: flex-start; */
}

.root {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>