<template>
  <video class="video" id="wowza-sender" autoplay playsinline muted></video>
</template>

<script>
import * as Sender from "../utils/webrtc/publish";
import {
  EventBus,
  EVENT_PUBLISH_WEBRTC,
  EVENT_SHOW_SNACKBAR
} from "../utils/event-bus";

export default {
  created() {
    const onShow = message => {
      EventBus.$emit(EVENT_SHOW_SNACKBAR, message);
    };

    const onHide = () => {
      console.log("### onHide");
    };

    this.$nextTick(() => {
      console.log("Sender Created");
      Sender.initialize(onShow, onHide);
    });

    EventBus.$on(EVENT_PUBLISH_WEBRTC, () => {
      console.log("RECEIVE PUBLISH");
    });
  }
};
</script>

<style scoped>
.video {
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg); /* Safari and Chrome */
  -moz-transform: rotateY(180deg); /* Firefox */

  /* width: 400px;
  border-radius: 100px; */
}
</style>
