let saved_storage_data = {};

const STORAGE_NAME = "IMFINE_SD_STORAGE";

export const STR_USER_NAME = "STR_USER_NAME";
export const STR_WEB_RTC_URL = "STR_WEB_RTC_URL";
export const STR_WEB_RTC_RECEIVE_STREAM = "STR_WEB_RTC_RECEIVE_STREAM";
export const STR_WEB_RTC_SENDER_STREAM = "STR_WEB_RTC_SENDER_STREAM";
export const STR_WEB_RTC_VIDEO_BITRATE = "STR_WEB_RTC_VIDEO_BITRATE";
export const STR_WEB_RTC_AUDIO_BITRATE = "STR_WEB_RTC_AUDIO_BITRATE";
export const STR_WEB_RTC_FRAME_SIZE = "STR_WEB_RTC_FRAME_SIZE";
export const STR_WEB_RTC_FRMAE_RATE = "STR_WEB_RTC_FRMAE_RATE";

export const Key = {
  STR_USER_NAME,
  STR_WEB_RTC_URL,
  STR_WEB_RTC_RECEIVE_STREAM,
  STR_WEB_RTC_SENDER_STREAM,
  STR_WEB_RTC_VIDEO_BITRATE,
  STR_WEB_RTC_AUDIO_BITRATE,
  STR_WEB_RTC_FRAME_SIZE,
  STR_WEB_RTC_FRMAE_RATE
};

export const Default = {
  STR_USER_NAME: "",
  STR_WEB_RTC_URL: "5f852799e11d1",
  STR_WEB_RTC_RECEIVE_STREAM: "",
  STR_WEB_RTC_SENDER_STREAM: "Test",
  STR_WEB_RTC_VIDEO_BITRATE: 2000,
  STR_WEB_RTC_AUDIO_BITRATE: 64,
  STR_WEB_RTC_FRAME_SIZE: "default",
  STR_WEB_RTC_FRMAE_RATE: 30
};

export function initialize() {
  console.log("SD CONFIG START");
  const data = localStorage.getItem(STORAGE_NAME);
  saved_storage_data = JSON.parse(data);
}

export function put(key, value) {
  saved_storage_data = { ...saved_storage_data, [key]: value };
  localStorage.setItem(STORAGE_NAME, JSON.stringify(saved_storage_data));
}

export function load(key) {
  const json = saved_storage_data;
  if (json && json[key]) {
    return json[key];
  } else {
    return Default[key];
  }
}
