<template>
  <div class="root">
    <Chatting class="chat" />
    <div class="count">{{ h_count }}</div>
    <component
      class="emoji_item"
      v-for="(item, key) in obj"
      :key="key"
      :ref="key"
      :is="item.component"
      @end_anim="deleteItem"
      :props_data="item.emoji"
    >
    </component>
  </div>
</template>

<script>
import Chatting from "@/components/Chatting.vue";
import EmojiIcon from "@/components/EmojiIcon.vue";
import { EventBus } from "../utils/event-bus";

export default {
  components: {
    Chatting,
    EmojiIcon
  },
  data() {
    return {
      h_count: 0,
      obj: {},
      video_width: 0,
      video_height: 0
    };
  },
  created() {
    this.video_width = window.innerWidth;
    this.video_height = (window.innerWidth * 9) / 16;

    EventBus.$on("RECIEVE_DATA", data => {
      this.obj["idx" + this.h_count] = {
        component: EmojiIcon,
        emoji: data.message
      };
      this.h_count++;
    });
  },
  methods: {
    deleteItem(payload) {
      this.$delete(this.obj, payload);
    }
  }
};
</script>

<style scoped>
.root {
  position: absolute;
  width: 100%;
  height: 100%;
}

.chat {
}

.receiver_video {
  width: 100%;
  height: 100px;
}
</style>