<template>
  <div class="outline">
    <v-btn
      class="emoji_btn"
      icon
      :color="emoji_color"
      @click="setEmojiEnable(true)"
    >
      <v-icon>mdi-emoticon-outline</v-icon>
    </v-btn>
    <form class="form" @submit="sendMessage">
      <input
        ref="message_input"
        type="text"
        class="text_input"
        placeholder="Type message..."
        :value="input_data"
        @input="updataInput"
        @keyup.enter="sendMessage"
      />
    </form>
    <div @click="sendMessage" class="btn_cls">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        enable-background="new 0 0 24 24"
        height="24"
        viewBox="0 0 24 24"
        width="24"
      >
        <rect fill="none" height="24" width="24" x="0" y="0" />
        <path
          :style="{ fill: button_color }"
          d="M12,2C6.48,2,2,6.48,2,12c0,5.52,4.48,10,10,10s10-4.48,10-10C22,6.48,17.52,2,12,2z M13.88,11.54l-4.96,4.96l-1.41-1.41 l4.96-4.96L10.34,8l5.65,0.01L16,13.66L13.88,11.54z"
        />
      </svg>
    </div>
  </div>
</template>

<script>
import {
  EventBus,
  EVENT_EMOJI_ACTIVE,
  EVENT_UPDATE_LIST
} from "../utils/event-bus";

export default {
  data() {
    return {
      input_data: "",
      isEdit: false,
      button_color: "#9e9e9e",
      emoji_color: "#666666"
    };
  },
  methods: {
    sendMessage() {
      if (!this.input_data) return;

      const message = this.input_data;
      this.$store.dispatch("SEND_MESSAGE", message).then(error => {
        console.log("RR = ", error);

        if (!error) {
          this.input_data = "";
          this.isEdit = false;
          EventBus.$emit(EVENT_UPDATE_LIST);

          console.log(this.$refs.message_input);
          this.$refs.message_input.blur();
        }
      });
    },
    getBtnClass() {
      return this.$refs.input.value == true ? "send_btn" : "wait_btn";
    },
    updataInput(event) {
      this.input_data = event.target.value;
      this.isEdit = this.input_data.length > 0;
      this.button_color = this.isEdit ? "#03dac5" : "#9e9e9e";
    },
    setEmojiEnable(isActive) {
      console.log(isActive);
      const active = this.emoji_color === "#666666";

      EventBus.$emit(EVENT_EMOJI_ACTIVE, active);
      this.emoji_color = active ? "#03DAC5" : "#666666";
    }
  }
};
</script>

<style scoped>
.outline {
  background-color: white;
  display: flex;
}

.emoji_btn {
  margin-top: 7px;
}

.form {
  flex: 1;
}

.text_input {
  width: 100%;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

input::placeholder {
  color: #dedede;
}

input:focus,
input.form-control:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

.btn_cls {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
}
</style>
