<template>
  <div ref="bg" class="tab_bg">
    <ChattingList class="list"></ChattingList>
    <div class="shadow_up"></div>
    <ChattingInput class="input"></ChattingInput>
    <div v-if="isEmojiEnable" class="emoji_list">
      <div class="box">
        <div
          v-for="item in getEmojiItem"
          :key="item.index"
          class="emoji_item"
          @click="ADD_EMOJI(item)"
        >
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ChattingInput from "@/components/ChattingInput.vue";
import ChattingList from "@/components/ChattingList.vue";
import { mapActions, mapGetters } from "vuex";
import { EventBus, EVENT_EMOJI_ACTIVE } from "../utils/event-bus";

export default {
  components: { ChattingInput, ChattingList },
  data() {
    return {
      isEmojiEnable: false
    };
  },
  created() {
    this.$nextTick(() => {
      const y = this.$refs.bg.getBoundingClientRect().top;
      const height = window.innerHeight - y;
      console.log(y, height);
      this.$refs.bg.style.height = height - 50 + "px";
    });

    EventBus.$on(EVENT_EMOJI_ACTIVE, isActive => {
      this.isEmojiEnable = isActive;
    });
  },
  computed: {
    ...mapGetters(["getEmojiItem"])
  },
  methods: {
    ...mapActions(["ADD_EMOJI"]),
  }
};
</script>

<style scoped>
.tab_bg {
  overflow-y: scroll;
  width: 100%;
  background-color: #f5f6f1;
}

.list {
  width: 100%;
}

.input {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
}

.shadow_up {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  box-shadow: 0px 1px 10px lightgray;
}

.emoji_list {
  width: 100%;
  height: 120px;
  position: fixed;
  bottom: 60px;
}

.box {
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 5px;
  top: 5px;
  background-color: white;

  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  box-shadow: 5px 5px 5px 5px lightgrey;
  display: flex;
  overflow-x: auto;
  scrollbar-width: none;
}

.emoji_item {
  margin: 10px 20px;
  font-size: 60px;
  width: 100px;
}
</style>
