<template>
  <div class="child" :style="contents_style">
    <img class="logo" src="../assets/LOGO-1.gif" />
    <div class="version">Build ({{ this.app_version }})</div>
    <div class="field_list">
      <v-text-field v-model="user_code" label="CODE" outlined></v-text-field>
      <v-text-field
        v-model="user_name"
        value="John Doe"
        label="NICK NAME"
        outlined
      ></v-text-field>
    </div>
    <v-btn class="btn" text color="#03DAC5" @click="gotoPreset">
      <div class="font">CONTINUE</div>
    </v-btn>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { activateUser } from "../api/firebase";

export default {
  data() {
    return {
      user_code: "",
      user_name: "",
      app_version: 0,
      contents_style: {
        height: document.documentElement + "px"
      }
    };
  },
  created() {
    this.app_version = process.env.VUE_APP_VERSION_CODE;
    activateUser();
  },
  methods: {
    ...mapMutations(["SET_NAME", "SET_CODE"]),
    gotoPreset() {
      if (!this.user_code || !this.user_name) return;

      this.SET_NAME(this.user_name);
      this.SET_CODE(this.user_code);
      this.$router.push("/event/preset");
    },
    submit() {
      this.$v.$touch();
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.select = null;
      this.checkbox = false;
    }
  }
};
</script>

<style scoped>
.child {
  /* background-color: darkgoldenrod; */
  position: absolute;
  height: 300px;
  width: 80%; /* 정렬하려는 요소의 넓이를 반드시 지정 */
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.logo {
  width: 80%;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
  image-rendering: pixelated;
}

.field_list {
  /* background-color: darkgray; */
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  height: 150px;
}

.v-text-field--outlined >>> fieldset {
  border-color: rgba(0, 0, 0, 0.12);
}

.version {
  color: #9e9e9e;
  font-size: 12px;
}

.btn {
  position: absolute;
  bottom: 0;
  right: 0;
}

.font {
  margin: auto -10px;
  font-size: 15px;
}
</style>